@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.HexToRgb{width:100%;max-width:100%;height:750px;background-color:#f0681a;padding-top:100px}.HexToRgb-row{max-width:500px;height:300px;margin:0 auto;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#ffffff}.HexToRgb-input{border:2px solid black;width:200px;height:50px;font-size:30px}.HexToRgb-label{display:block;font-size:24px;text-align:center;margin-top:5px;border:1px solid black;background:rgba(0,6,8,0.37);padding:5px;height:40px}

.Steps{max-width:100%;width:100%;height:700px;padding-left:1%;padding-right:1%}.Steps-row{max-width:600px;width:100%;margin:0 auto;margin-top:50px;display:flex;flex-direction:column}

.Steps-form{display:flex;padding:10px;justify-content:space-between;align-items:flex-end;max-width:100%}.form-item{display:flex;flex-direction:column}.input-item{height:40px;padding:5px;padding-left:15px;padding-right:15px;border:1px solid black;border-radius:10px;font-size:1.2em;display:block}.lable{display:block;margin-bottom:10px}.fild{max-width:33%}::-webkit-inner-spin-button{display:none}::-webkit-calendar-picker-indicator{cursor:pointer}.input-button{padding-left:15px;padding-right:15px}@media (max-width: 450px){.input-item{font-size:1em}.lable{font-size:0.7em}}

.output{margin-top:30px}.output-title{display:flex;justify-content:space-between}.output-body{margin-top:20px;padding:15px;border:1px solid black;border-radius:10px;display:flex;flex-direction:column}

.output-item{display:flex;justify-content:space-between;align-items:center;margin-bottom:10px}.output-item:last-child{margin-bottom:0px}.clear{margin-left:10px}.clear:hover{color:red}.edit:hover{color:green}.material-icons{cursor:pointer}

